.Identidade {
  width: 100%;
  height: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 10px 0px 70px 0px;
}
.container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
}
#tag-verde {
  color: #10702e;
  position: relative;
  left: -100px;
}
.description {
  width: 55%;
  font-size: 1rem;
  font-weight: 300;
}
@media screen and (max-width: 600px) {
  .container {
    flex-direction: row;
  }
  #tag-verde {
    left: 0px;
  }
}
