.Banner {
  z-index: -1;
  width: 100%;
  height: 380px;
  overflow: hidden;
}
.container {
  height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: space-evenly;
  align-items: center;
}
.content:nth-child(2) {
  position: relative;
  left: -80px;
}
.content {
  width: 280px;
  height: 290px;
  position: relative;
}
#cachorro {
  width: 120%;
}
#gatoDois {
  width: 69%;
  position: absolute;
  bottom: -79px;
}
.verde-fina {
  color: #10702e;
  font-weight: 700;
}
.verde-grossa {
  color: #10702e;
  font-weight: 900;
  font-size: 3.5rem;
  position: relative;
  top: 13px;
}
.preta-regular {
  color: #1d1d1d;
  font-size: 2.3em;
}
.line-top {
  position: relative;
}
.line-center {
  position: relative;
  top: -50px;
}
.line-end {
  position: relative;
  top: -120px;
}
@media screen and (max-width: 860px) {
  .none-dog {
    display: none;
  }
  .content:nth-child(2) {
    position: relative;
    left: 0px;
  }
}
@media screen and (max-width: 749px) {
  .none-gato {
    display: none;
  }
  .content:nth-child(2) {
    position: relative;
    left: 35px;
  }
  .Banner {
    height: 240px;
  }
  .none-dog {
    display: initial;
  }
  #cachorro {
    width: 80%;
    position: relative;
    top: 50px;
    right: -50px;
  }
}
@media screen and (max-width: 370px) {
  .none-dog {
    display: none;
  }
}
