.a-header {
  text-decoration: none;
  font-weight: 500;
  color: #060709;
  font-size: 1.1em;
  border-bottom: 1px solid #f5f5f5;
  border-radius: 5px;
  transition: 0.3s ease-out;
}
.a-header:hover {
  color: #10702e;
  border-bottom: 1px solid #10702e;
}
.Header {
  width: 100%;
  height: 80px;
}
.nav-bar {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 16px 24px;
}
#logoHorizontal {
  width: 180px;
}
.nav-container {
  width: 40%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: auto;
}
.nav-link {
  display: inline-block;
}
@media screen and (max-width: 749px) {
  .nav-container {
    display: none;
  }
}
