.Servicos {
  width: 100%;
  height: fit-content;
  padding: 22px 0px;
  background-color: #1adb4e;
}
.servicos-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  gap: 12px;
}
.card-servicos {
  position: relative;
  width: 265px;
  height: 373px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 23px;
  overflow: hidden;
  box-shadow: 3px 3px 16px 12px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.titulo-servico {
  margin: 0px 25px;
  position: relative;
}
.h2-servicos {
  color: #1d1d1d;
  font-size: 1.3em;
  width: 140px;
}
.btn-text {
  color: #f5f5f5;
  font-weight: 400;
  border: none;
  text-decoration: none;
}
.btn-container {
  cursor: pointer;
  margin: 0px 25px;
  background-color: #10702e;
  border-radius: 14px;
  width: fit-content;
  padding: 4px 21px;
  position: relative;
  top: 90px;
}
.icon-img {
  position: relative;
  width: 60px;
}
.icon-servico {
  width: 100%;
  height: fit-content;
  display: flex;
  justify-content: center;
}
