.Sobre {
  width: 100%;
  padding: 50px 0px;
  background-color: #10702e;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}
#garotaDog {
  width: 260px;
  border-radius: 12px;
}
#gato-um {
  width: 80px;
}
.sobre-text {
  padding: 10px 12px;
  width: 280px;
}
.sobre-text p,
h2 {
  color: #fff;
}
h2 {
  position: relative;
  top: 15px;
  font-weight: 800;
  font-size: 2rem;
}
p {
  text-align: left;
  font-size: 1rem;
  font-weight: 300;
}
@media screen and (max-width: 860px) {
  .none-gatoum {
    display: none;
  }
}
#link-foto {
  position: absolute;
  left: 250px;
  bottom: -176px;
  font-size: 6px;
  color: #fff;
}
@media screen and (max-width: 749px) {
  .none-gatoum {
    display: initial;
  }
  .Image {
    display: none;
  }
}
